import api from '../api/http.js'
import axios from "axios";
export default {
  nameActive: (state, status) => { //学科栏目选中状态
    state.nameActive = true;
  },
  fetchloading: (state, statues) => { // loading
    state.fetchLoading = statues
  },
  setKeyword: (state, keyword) => {
    state.keyword = keyword; //设置关键字
  },
  setsearchAdvancedParams: (state, params) => { //高级搜索条件
    state.searchAdvancedParams = params;
  },
  setworkTitlesparams: (state, params) => { //职称
    state.workTitlesparams = params
  },
  setcountrsparams: (state, params) => { //国籍 
    state.countrsparams = params
  },
  setareaparams: (state, params) => { //地域 
    state.areaparams = params
  },
  setxuekeparams: (state, params) => { //学科
    state.xuekeparams = params
  },
  setclickMoresize: (state, params) => { //初始化更多按钮条数
    state.clickMoresize = params
  },
  setflg: (state, flg) => {
    state.flg = flg
  },
  clearpropsParams: (state) => {
    state.workTitlesparams = ''; //清空职称
    state.countrsparams = ''; //清空国籍
    state.areaparams = ''; //清空地域 
    state.xuekeparams = ''; //清空学科
  },
  clearkeySerach: (state) => {
    state.keyword = '';
  },
  clearKeypage: (state) => { //清空关键字搜索页数
    state.keysearchTotalPage = 0;
  },
  clearAdvancedpage: (state) => { //清空高级搜索页数
    state.AdvancedsearchTotalPage = 0;
  },
  search_More: (state, obj) => { //搜索更多
    var obj = obj;
    if (obj) {
      obj = obj;
    } else {
      obj = {
        size: 12,
        page: 0
      };
    };
    state.fetchLoading = true;
    api.emitAjax({
      path: "/api/front/psons/search",
      type: "GET",
      data: obj,
      headers: null,
      success: function (response) {
        state.fetchLoading = false;
        state.searchAdvancedList = response.content;
        state.AdvancedsearchTotalPage = response.totalPages; //总页数
        state.totalElements_ = response.totalElements;
      }
    })
  },
  getController: (state, params) => { //地域
    var url = "/api/front/areas";
    if (params) {
      var params = {
        page: -1,
        size: -1,
        level: params
      };
    } else {
      var params = {
        page: -1,
        size: -1
      };
    };
    api.emitAjax({
      path: url,
      type: "GET",
      data: params,
      headers: null,
      success: function (response) {
        state.controllerA = response.content;
      }
    })
  },
  getControllerB: (state, params) => { //地域B
    var url = "/api/front/areas";
    var params = {
      page: -1,
      size: -1,
      parent: params
    };
    api.emitAjax({
      path: url,
      type: "GET",
      data: params,
      headers: null,
      success: function (response) {
        state.controllerB = response.content;
      }
    })
  },

  getCountrys: (state, params) => { //国籍
    var url = "/api/front/countrys";
    var params = {
      page: -1,
      size: -1,
      countrySimpleCnName: params
    };
    api.emitAjax({
      path: url,
      type: "GET",
      data: params,
      headers: null,
      success: function (response) {
        state.countrys = response.content;
      }
    })

  },
  getcolleges: (state, params) => {
    //单位
    var url = "/api/front/datas/colleges";
    var params = {
      page: -1,
      size: -1,
      collegeName: params
    };
    api.emitAjax({
      path: url,
      type: "GET",
      data: params,
      headers: null,
      success: function (response) {
        state.companys = response;
      }
    })
  },
  getCommoncode: (state, paramsMap) => {
    var name = paramsMap.objs.name;
    var params = {};
    switch (name) {
      case "work_title":
        params.codetype = 1;
        break;
      case "learn_degree":
        params.codetype = 2;
        break;
      case "subject":
        params.codetype = 3;
        break;
    };
    api.emitAjax({
      path: "/api/front/codes/key/" + name + "/codes",
      type: "GET",
      data: params,
      success: function (response) {
        if (response) {
          switch (name) {
            case 'companys': //单位
              state.companys = response.content
              break;
            case 'work_title': //职称
              var _array1 = [],
                _array2 = [],
                old_array = response.content;
              for (var i = 0; i < old_array.length; i++) {
                switch (old_array[i].codename) {
                  case '助理级':
                    _array1.push({
                      codekey: old_array[i].codekey,
                      codename: '中级以下'
                    });
                    break;
                  case '员级':
                    _array1.push({
                      codekey: old_array[i].codekey,
                      codename: '中级以下'
                    });
                    break;
                  case '未定级':
                    _array1.push({
                      codekey: old_array[i].codekey,
                      codename: '中级以下'
                    });
                    break;
                  case 'Assistant':
                    _array1.push({
                      codekey: old_array[i].codekey,
                      codename: 'Below Medium-Grade'
                    });
                    break;
                  case 'Section Officer':
                    _array1.push({
                      codekey: old_array[i].codekey,
                      codename: 'Below Medium-Grade'
                    });
                    break;
                  case 'None':
                    _array1.push({
                      codekey: old_array[i].codekey,
                      codename: 'Below Medium-Grade'
                    });
                    break;
                  default:
                    _array2.push({
                      codekey: old_array[i].codekey,
                      codename: old_array[i].codename
                    });
                    break;
                }
              };
              var unitarray = {
                codekey: _array1[0].codekey + "," + _array1[1].codekey + "," + _array1[2].codekey,
                codename: _array1[0].codename
              };
              _array2.splice(3, 0, unitarray);
              state.workTitles = _array2
              break;
            case 'learn_degree': //学位
              state.learnDegrees = response.content
              break;
            case 'subject': //学科
              state.subjects = response.content
              break;
          }
        }
      }
    })
  },
  //首页学科
  getNationality: (state, ops) => {
    var params = {};
    var keyid = ops.firstkeyId;
    var url = "/api/front/codes/key/" + keyid + "/codes";
    if (ops.twokeyId) {
      var keyid = ops.twokeyId;
      var url = "/api/front/codes/key/" + keyid + "/codes";
      params.codetype = 3;
    }
    api.emitAjax({
      path: url,
      type: "GET",
      data: params,
      headers: null,
      success: function (response) {
        if (response) {
          if (ops.twokeyId) {
            state.NationalityTwo = response.content;
          } else {
            state.Nationality = response.content;
          }
        }
      }
    })
  },
  sethotList: (state, listdata) => {
    state.hotList = listdata //首页或者头部搜索无关键字
    state.hotListBACKUP = listdata;
  },
  clearhotList: (state) => {
    state.hotList = [] //清空
  },
  clearhotListBACKUP: (state) => {
    state.hotListBACKUP = [] //清空备份
  },
  getLoginUser(state, data) {
    state.userInfo = data;
  },
  setShowMenuStatus:(state,flg)=>{
    state.ShowMenuStatus=flg //头部菜单是否显示--移动端
  }
}



