export default {
    // 请求数据时状态loading
    fetch_loading({
        commit
    }, status) {
        commit('fetchloading', status)
    },
    Asynchronous_advanced({
        commit
    }, params) { //异步 高级搜索(侧边条件)
        commit('search_More', params);
    }

}


