/*
	1:路由懒加载:
	用vue.js写单页面应用时，会出现打包后的JavaScript包非常大，
	影响页面加载，我们可以利用路由的懒加载去优化这个问题
	当我们用到某个路由后，才去加载对应的组件，这样就会更加高效
*/
import Vue from 'vue'
import Router from 'vue-router'
import config from '../config'
Vue.use(Router);
/*首页*/
const Index = (resolve) => {
    import('@/components/index/index').then((module) => {
        resolve(module)
    })
}

/*热点*/
const Hot = (resolve) => {
    import('@/components/index/hot/index').then((module) => {
        resolve(module)
    })
}

/*学科*/
const Nation = (resolve) => {
    import('@/components/index/nationality/index').then((module) => {
        resolve(module)
    })
}

const Nation_list = (resolve) => {
    import('@/components/index/nationality/list').then((module) => {
        resolve(module)
    })
}

/*职称*/
const Profess = (resolve) => {
    import('@/components/index/professional/index').then((module) => {
        resolve(module)
    })
}

/*国籍*/
const Subject = (resolve) => {
    import('@/components/index/subject/index').then((module) => {
        resolve(module)
    })
}

/*地域*/
const Territory = (resolve) => {
    import('@/components/index/territory/index').then((module) => {
        resolve(module)
    })
}

/*高级搜索*/
const Search = (resolve) => {
    import('@/components/index/search/index').then((module) => {
        resolve(module)
    })
}

/*人物新闻*/
const personageNews = (resolve) => {
    import('@/components/personageNews/index').then((module) => {
        resolve(module)
    })
}

//人物新闻
const personageNewsList = (resolve) => {
    import('@/components/personageNews/list.vue').then((module) => {
        resolve(module)
    })
}

//人物新闻详细
const personageNewsContent = (resolve) => {
    import('@/components/personageNews/content.vue').then((module) => {
        resolve(module)
    })
}

/*平台统计*/
const ComputedData = (resolve) => {
    import('@/components/computedData/index').then((module) => {
        resolve(module)
    })
}


/*404页面没找到*/
const NotFind = (resolve) => {
    import('@/components/common/404').then((module) => {
        resolve(module)
    })
}
/*个人主页暂未开放*/
const NoOpen = (resolve) => {
    import('@/components/common/NoOpen').then((module) => {
        resolve(module)
    })
}


export default new Router({
    // mode: "history",
    base:config.pathName,
    routes: [{
        path: '/',
        name: 'home',
        component: Index,
        redirect: "/hot",
        children: [{
            path: "hot",
            component: Hot,
            alias: '/hota',
            name: "hot",
            meta: {
                title: {
                    cn: "热点",
                    en: "Popular"
                }
            }
        },
        {
            path: "/nation",
            component: Nation,
            name: "nation",
            meta: {
                title: {
                    cn: "学科",
                    en: "Subject"
                },
                active: 'nation_css'
            }
        },
        {
            path: "/profess",
            component: Profess,
            name: "profess",
            meta: {
                title: {
                    cn: "职称",
                    en: "Job Title"
                },
            }
        },
        {
            path: "/subject",
            component: Subject,
            name: "subject",
            meta: {
                title: {
                    cn: "国籍",
                    en: "Country of Citizenship"
                },
            }
        },
        {
            path: "/territory",
            component: Territory,
            name: "territory",
            meta: {
                title: {
                    cn: "地域",
                    en: "Origin"
                },
            }
        }
        ]
    },
    {
        path: "/nation/Subjectlist",
        component: Nation_list,
        name: "natinalList",
        meta: {
            title: {
                cn: "学科详情",
                en: "Subject Detail"
            },
            active: 'nation_css'
        }
    },
    {
        path: "/search",
        component: Search,
        name: "search",
        meta: {
            title: {
                cn: "高级搜索",
                en: "Advanced Search"
            },
        }
    },
    {
        path: "/personageNew",
        component: personageNews,
        name: "personageNew",
        meta: {
            title: {
                cn: "人物新闻",
                en: "News of Figures"
            },
        },
        redirect: "/personageNewList",
        children: [{
            path: "/personageNewList",
            component: personageNewsList,
            name: "personageNewsList",
            meta: {
                title: {
                    cn: "人物新闻列表页",
                    en: "News of Figures"
                },
                pagehide: true //分页显示
            }
        },
        {
            path: "/personageNew/:id",
            component: personageNewsContent,
            name: "personageNewsContent",
            meta: {
                title: {
                    cn: "人物新闻详情页",
                    en: "News of Figures"
                },
                pagehide: false //分页不显示
            }
        }
        ]
    },{
        path: "/computedData",
        component: ComputedData,
        name: "ComputedData",
        meta: {
            title: {
                cn: "平台统计",
                en: "Platform Statistics"
            },
        }
    },
    {
        path: '*',
        component: NotFind,
        meta: {
            title: {
                cn: "404未找到",
                en: "404"
            },
        },
    },
    {
        
        path: '/noOpen',
        component: NoOpen,
        meta: {
            title: {
                cn: "个人主页暂未开放！",
                en: "The personal home page is not open temporarily！"
            },
        },
    },
    /* -------------英文网-------------------- */
    {
        path: '/en',
        name: 'enhome',
        component: Index,
        redirect: "/en/hot",
        children: [{
            path: "/en/hot",
            component: Hot,
            name: "enhot",
            meta: {
                title: {
                    cn: "热点",
                    en: "Popular"
                }
            }
        },
        {
            path: "/en/nation",
            component: Nation,
            name: "ennation",
            meta: {
                title: {
                    cn: "学科",
                    en: "Subject"
                },
                active: 'nation_css'
            }
        },
        {
            path: "/en/nation/Subjectlist",
            component: Nation_list,
            name: "ennatinalList",
            meta: {
                title: {
                    cn: "学科详情",
                    en: "Subject Detail"
                },
                active: 'nation_css'
            }
        },
        {
            path: "/en/profess",
            component: Profess,
            name: "enprofess",
            meta: {
                title: {
                    cn: "职称",
                    en: "Job Title"
                },
            }
        },
        {
            path: "/en/subject",
            component: Subject,
            name: "ensubject",
            meta: {
                title: {
                    cn: "国籍",
                    en: "Country of Citizenship"
                },
            }
        },
        {
            path: "/en/territory",
            component: Territory,
            name: "enterritory",
            meta: {
                title: {
                    cn: "地域",
                    en: "Origin"
                },
            }
        }
        ]
    },
    {
        path: "/en/search",
        component: Search,
        name: "ensearch",
        meta: {
            title: {
                cn: "高级搜索",
                en: "Advanced Search"
            },
        }
    },
    {
        path: "/en/personageNew",
        component: personageNews,
        name: "enpersonageNew",
        meta: {
            title: {
                cn: "人物新闻",
                en: "News of Figures"
            },
        },
        redirect: "/en/personageNewList",
        children: [{
            path: "/en/personageNewList",
            component: personageNewsList,
            name: "enpersonageNewsList",
            meta: {
                title: {
                    cn: "人物新闻列表页",
                    en: "News of Figures"
                },
                pagehide: true //分页显示
            }
        },
        {
            path: "/en/personageNew/:id",
            component: personageNewsContent,
            name: "enpersonageNewsContent",
            meta: {
                title: {
                    cn: "人物新闻详情页",
                    en: "News of Figures"
                },
                pagehide: false //分页不显示
            }
        }
        ]
    },{
        path: "/en/computedData",
        component: ComputedData,
        name: "enComputedData",
        meta: {
            title: {
                cn: "平台统计",
                en: "Platform Statistics"
            },
        }
    }]
})


