import md5 from "crypto-js/md5";
import config from "../config";
import axios from "axios";

/* 一些自定义的方法 */

/* 
    使用axios发起网络请求
*/
function emitAjax(opts) {
    const app_secret = "6d3121b650e42855976d0f70dd2048e4";
    const app_key = "cgsoft";
    const timestamp = Date.parse(new Date());
    let headers = {};
    let requestData = {};
    let lang = "cn";
    for (let index = 0; index < config.langTypeArray.length; index++) {
        const langType = config.langTypeArray[index];
        if(window.location.pathname.indexOf("/"+langType+"/")>=0){
            lang = langType;
            break;
        }
    }
    //定义默认参数
    let defaultOption = {
        path: "", //请求地址
        type: "GET", //请求方式
        data: {},
        dataType: "common", //文件上传还是普通数据交互
        headers: null,
        downFile: false,   //是否是下载文件
        success: function (result) {   //code200执行的函数
            console.log('数据返回成功', result)
        },
        error: function (result) { //不是code200 执行的函数
            console.log('数据返回失败', result);
        }
    }
    //合并参数
    const opt = Object.assign({}, defaultOption, opts);
    opt.data.lang = lang;
    try {
        delete opt.data.gmtCreate;
        delete opt.data.gmtModified;
        delete opt.data.id;
        delete opt.data._index;
        delete opt.data._rowKey;
    } catch (error) {}
    /* 
        生成签证 
        合并请求头
    */
    const sign = setSign(app_secret, app_key, timestamp, opt.path, opt.data);
    headers = Object.assign({}, headers, opt.headers);

    /* 
        组织请求参数
        根据是否是文件上传请求做判断
        如果是文件上传 opt.data应是一个 FormData对象
    */
    if (opt.dataType == "file") {
        opt.data.append("app_key", app_key);
        opt.data.append("timestamp", timestamp);
        opt.data.append("sign", sign);
        requestData = opt.data;
    } else if (opt.type == "POST"|| opt.type == 'PUT') {
        const params = new URLSearchParams();
        params.append("app_key", app_key);
        params.append("timestamp", timestamp);
        params.append("sign", sign);
        for (const key in opt.data) {
            if (opt.data.hasOwnProperty(key)) {
                const value = opt.data[key];
                if(value==0 || value&&value!="null"){
                    params.append(key, value);
                }
            }
        }
        requestData = params;
    } else {
        requestData = Object.assign({}, {
            app_key,
            timestamp,
            sign,
        }, opt.data);
    }

    //发送请求
    let axiosOption = {
        baseURL: config.serverUrl,
        url: opt.path,
        method: opt.type,
        headers,
    }
    if(opt.responseType){
        axiosOption.responseType = opt.responseType;
    }
    if ( opt.type == "POST" || opt.type == 'PUT') {
        axiosOption.data = requestData;
    } else {
        axiosOption.params = requestData;
    }
    axios(axiosOption).then(({ data }) => {
        const response = data;
        if (typeof response == "string") {
            resultObj = JSON.parse(response);
        }
        switch (response.code) {
            case 200:
                const successOpt = opt.responseType == 'blob' ? response : response.data;
                opt.success(successOpt);
                break;
            case 401:
                //登录失败
              //  alert(response.message);
                break;
            case 408:
                delLocalData();
                window.location.href = "/login";
            default:
              //  alert(response.message);
                opt.error(response);
                break;
        }
    }).catch(function (error) {
       // console.log(error)
        if(error.toString().indexOf("Network Error") >=0){
          //  alert("网络请求有误，请重试");
        }
       // opt.error(error);
    });
}
//签证
function setSign(app_secret, app_key, timestamp, path, opt) {
    let keyArray = [];
    let signString = app_secret + path;
    if (typeof opt == "object") {
        for (const key in opt) {
            if (opt.hasOwnProperty(key)) {
                keyArray.push(key);
            }
        }
        keyArray.sort();
        for (let index = 0; index < keyArray.length; index++) {
            const key = keyArray[index];
            if (opt[key] !== '' && opt[key] !== null && opt[key] !== undefined) {
                signString += key + opt[key];
            }
        }
    }
    signString += timestamp + " " + app_secret;
    return md5(signString).toString();
}

//设置本地缓存
function setLocalData(data) {
    for (const key in data) {
        if (data.hasOwnProperty(key)) {
            const value = data[key];
            localStorage.setItem(key, value);
        }
    }
}
//删除缓存
function delLocalData(array) {
    if (array == "all") {
        const rememberMe = localStorage.getItem("rememberMe");
        localStorage.clear();
        rememberMe && localStorage.setItem("rememberMe", rememberMe);
    } else {
        for (let index = 0; index < array.length; index++) {
            const key = array[index];
            localStorage.removeItem(key);
        }
    }
}

//字符串去掉空格
function replaceSpace(string){
    return string.replace(/(^\s*)|(\s*$)/g, "");
}
//表单验证函数
function verificationForm(verArray){
    for (let index = 0; index < verArray.length; index++) {
        const value = verArray[index];
        if(!value.object){
            alert(value.message);
            return false;
        }
    }
    return true;
};
function getCookie(name,isescape){
    const cookie = document.cookie;
    if(cookie.length >0){
        //查询cookie开始位置
        let start = cookie.indexOf(name+"=");
        if(start>=0){
            //=号后面的位置为开始位置
            start = start + name.length+1;
            //从name开始第一个;的位置
            let end = cookie.indexOf(";",start);
            if(end < 0 ) {
                end = cookie.length;
            }
            //cookie是否经过转码
            return isescape?unescape(cookie.substring(start,end)):cookie.substring(start,end);
        }
    }
    return "";
}
function loadCss(lang){
    const headEl = document.querySelector("head");
    const langCss = document.getElementById("langCss");
    if (lang != "cn") {
        if(langCss){return false};
        const link = document.createElement("link");
        link.setAttribute("href", "/static/css/" + lang + ".css");
        link.setAttribute("id", "langCss");
        link.setAttribute("rel", "stylesheet");
        headEl.appendChild(link);
    }else{
        if(langCss){
            headEl.removeChild(langCss);
        }
    }
}
function selectLangType(callback){
    //判断是那种语言
    let isCn = true;
    for (let index = 0; index < config.langTypeArray.length; index++) {
        const langType = config.langTypeArray[index];
        if (window.location.pathname.indexOf("/"+langType+"/") >= 0) {
            isCn = false
            loadCss(langType);
            callback(langType);
            break;
        }
    }
    if(isCn) {
        loadCss("cn");
        callback("cn");
    }
}
export default {
    emitAjax,
    setLocalData,
    delLocalData,
    replaceSpace,
    verificationForm,
    setSign,
    selectLangType
}



