/*fzb*/
import Vue from 'vue'
import store from './vuex'
import VueResource from 'vue-resource'
import iView from 'iview';
import "./assets/js/dataset"
import 'iview/dist/styles/iview.css';
import App from './App'
import router from './router/index.js'
import commonMethods from './api/http'
import echarts from 'echarts'
import langOption from "./assets/lang";
import config from "./config";
import './assets/style/index.css'      //主样式
import './assets/style/search.css'      //主样式
import Axios from 'axios'

import htmlToPdf from './api/html2canvas'
Vue.use(htmlToPdf)

Vue.use(iView);
/*组件使用中*/
Vue.use(VueResource)
//插件引用 全局变量配置

Vue.config.productionTip = false

//设置vue全局变量
Vue.prototype.$echarts = echarts //地图插件
Vue.prototype.commonMethods = commonMethods; //公用方法 包含http请求  本地储存等
Vue.prototype.langOption = langOption; //语言文件
Vue.prototype.config = config; //全局配置

//设置系统名称
const sysName = {
    cn: "浙江大学教师个人主页门户网",
    en: "Zhejiang University Personal homepage"
}

//跳转页面之前做一些操作
router.beforeEach((to, from, next) => {
    commonMethods.selectLangType(function(langType){
        document.title = to.meta.title[langType] + " - " + sysName[langType];
    })
    if (to.name != 'search') {
        //在高级检索跳转到其他路由的时候，及时清空检索所有的参数条件
        store.state.searchAdvancedParams = { page: 0, size: 12 };
        //从首页传到高级检索的参数，当路由跳转到其他页面的时候，也及时清空参数
        store.commit('clearpropsParams');
        //标记
        store.commit('setflg', true);
    };
    //清空（人物新闻菜单ID）缓存
    if (to.name != 'personageNewsList' && to.name != 'personageNewsContent') {
        sessionStorage.removeItem("columnId");
    };
    next();
})

//vue实例化
new Vue({
    el: '#app',
    router,
    store,
    components: {
        App
    },
    template: '<App/>'
})



